import React, { useState, useEffect, Suspense } from 'react'
import { connect } from 'react-redux'
import { setNotification } from '../../reducers/notificationReducer'
import { initializeSchoolClasses } from '../../reducers/schoolClassesReducer'
import schoolClassesService from '../../services/schoolClasses'

import { Link } from 'react-router-dom'
import { Container, ListGroup } from 'react-bootstrap'
import SchoolClass from './SchoolClass'
import LoadingIndicator from '../common/LoadingIndicator'
import CollapseForm from '../common/CollapseForm'

const LazySchoolClassForm = React.lazy(() => import('../forms/SchoolClassForm'))

const SchoolClassesList = ({
	user,
	schoolClasses,
	setNotification,
	initializeSchoolClasses
}) => {

	const [isLoading, setIsLoading] = useState(true)

	useEffect(() => {
		if (user) {
			schoolClassesService.setToken(user.token)
			initializeSchoolClasses()
				.catch(error => {
					setNotification({
						message: `   ,  :
							${error.status} ${error.statusText}`,
						variant: 'danger'
					}, 5)
				})
				.finally(() => setIsLoading(false))
		}
	}, [user, initializeSchoolClasses, setNotification])

	return (
		<Container>
			{isLoading
				? <LoadingIndicator
					animation="border"
					variant="primary"
				/>
				: <>
					<p className="pt-3 text-muted">
						  ,    ,
						   <Link to="/school/teachers"></Link>,&nbsp;
						<Link to="/school/specialties"></Link> &nbsp;
						<Link to="/school/pupils"></Link>    .
					</p>

					<CollapseForm
						title="  "
						ariaControls="school-class-add-form-collapse"
					>
						<Suspense
							fallback={
								<LoadingIndicator
									animation="border"
									variant="primary"
								/>}>
							<LazySchoolClassForm mode="create" />
						</Suspense>
					</CollapseForm>

					<p className="pt-3">
						   .
					</p>
					<ListGroup>
						{schoolClasses.map(schoolClass =>
							<ListGroup.Item
								className="px-0 py-1"
								key={schoolClass.id}
							>
								<SchoolClass schoolClass={schoolClass} />
							</ListGroup.Item>
						)}
					</ListGroup>
				</>
			}
		</Container>
	)
}

const mapStateToProps = (state) => {
	return {
		user: state.user,
		schoolClasses: state.schoolClasses
	}
}

const mapDispatchToProps = {
	setNotification,
	initializeSchoolClasses
}

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(SchoolClassesList)
